/**
 * Shows the My Account page and sets up the useAuth
 * hook to be used on ChangePassword, UpdateEmail and
 * DeleteAccount components.
 */

 import React, { useState, useContext, useEffect } from 'react';
 import { firebaseAuth } from '../../provider/AuthProvider';
 import { Link, Switch, Route, useHistory } from 'react-router-dom';
 import useAuth from '../../hooks/useAuth';
 
 import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
 import { faAngleRight, faTrash } from '@fortawesome/free-solid-svg-icons';
 
 import ChangePassword from './ChangePassword';
 import DeleteAccount from './DeleteAccount';
 import PageHeading from '../PageHeading';
 import UpdateProfile from './UpdateProfile';
import { useAlert } from 'react-alert';
import { dbMethods } from '../../firebase/dbMethods';
 
 const Upgrade = ({
    userStatus,
  }) => {
   const { user } = useContext(firebaseAuth);
   const [inputs, setInputs] = useState({ 
     email: user.email || "",
     password: "", 
     newPassword: "", 
     username: user.displayName || "", 
     avatarUrl: user.photoUrl || ""});
 
   const { 
     loading, 
     error, 
     status, 
     handleChangeEmail, 
     handleChangePassword, 
     handleDeleteAccount,
     resetStatus
   } = useAuth(inputs.username, inputs.email, inputs.password, inputs.newPassword);
  
   const history = useHistory();

   const alert = useAlert();
 
   const handleChange = e => {
     const { name, value } = e.target;
     setInputs(prev => ({ ...prev, [name]: value }));
   }
 
   if (!user) {
     history.push("/log-in");
   }

   const isPrenium = () => {
       return userStatus?.isPrenium ?? false
    }
 
   return (
     <div className="my-account">
       <div className="my-account-inner" style={{width: '50%', margin: '0 auto'}}>
         <Switch>
           <Route exact path="/upgrade">
             <div>
               <PageHeading
                 title=""
               />
               <div style={{display: 'flex', flexDirection: 'row', alignItems: 'center', justifyContent: 'space-between'}}>
                 <div>{`: ${userStatus?.coin ?? 0} `}</div>
                 <button style={{width: 100, height: 30, borderRadius: 10, background: 'green', color: 'white', fontSize: 10}} onClick={() => {
                   history.push('/buy-coin')
                 }}></button>
               </div>
               <h3>
                   {isPrenium() ? "" : ""}
               </h3>
               {!isPrenium() && <div className="account-data">
                   <h4>100/ </h4>
                   <h5 style={{color: 'green'}}></h5>
                   <h5 style={{color: 'orange'}}></h5>
                   <button className="btn btn-tertiary" onClick={() => {
                       if ((userStatus?.coin ?? 0) < 100) {
                           alert.show("")
                       } else {
                           dbMethods.updateUser(user, userStatus?.coin - 100, true)
                       }
                   }}>
                     <span>100/</span><FontAwesomeIcon icon={faAngleRight} className="icon" />
                   </button>
               </div>}
               <h4>500/VIP </h4>
                   <h5 style={{color: 'green'}}></h5>
                   <h5 style={{color: 'orange'}}></h5>
                   <h5 style={{color: 'blue'}}>docpdf..</h5>
                   <button className="btn btn-tertiary" onClick={() => {
                       alert.show('')
                    }}>
                     <span>500/VIP</span><FontAwesomeIcon icon={faAngleRight} className="icon" />
                   </button>
             </div>
           </Route>
 
           <Route exact path="/my-account/change-email">
             <UpdateProfile 
               handleChange={handleChange}
               inputs={inputs}
               loading={loading}
               onSubmit={handleChangeEmail}
               error={error}
               status={status}
               onBack={resetStatus}
             />
           </Route>
 
           <Route exact path="/my-account/change-password">
             <ChangePassword 
               handleChange={handleChange}
               loading={loading}
               inputs={inputs}
               onSubmit={handleChangePassword}
               error={error}
               status={status}
             />
           </Route>
           
           <Route exact path="/my-account/delete-account">
             <DeleteAccount 
               handleChange={handleChange}
               inputs={inputs}
               onSubmit={handleDeleteAccount}
               error={error}
             />
           </Route>
         </Switch>
       </div>
     </div>
   );
 }
 
 export default Upgrade;